@charset "UTF-8";
/* ------------------------------------ *\
  HEADER
\* ------------------------------------ */
.fr-header {
  --link-underline: none;
  --link-blank-font: none;
  --link-blank-content: none;
  --ul-type: none;
  --ol-type: none;
  --ul-start: 0;
  --ol-start: 0;
  --xl-block: 0;
  --li-bottom: 0;
  --ol-content: none;
  --text-spacing: 0;
  --title-spacing: 0;
  width: 100%;
  box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.1), 0 8px 16px -16px rgba(0, 0, 0, 0.32);
  position: relative;
}

.fr-header__body-row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  margin: 0 -1rem;
}

@media (min-width: 62em) {
  .fr-header__body-row {
    padding: 1.5rem 0;
  }
}

.fr-header__brand {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
  width: 100%;
}

@media (min-width: 62em) {
  .fr-header__brand {
    margin-top: -1rem;
    margin-bottom: -1rem;
  }
}

@media (min-width: 62em) {
  .fr-header__brand {
    flex-wrap: nowrap;
    width: auto;
  }
}

.fr-header__brand-top {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
}

@media (min-width: 62em) {
  .fr-header__brand-top {
    width: auto;
  }
}

@media (hover: hover) and (pointer: fine) {
  .fr-header__brand.fr-enlarge-link a[href]:hover {
    --a: 0.1;
  }
  .fr-header__brand.fr-enlarge-link a[href]:active {
    --a: 0.2;
  }
}

@media (min-width: 62em) {
  .fr-header__logo, .fr-header__operator, .fr-header__service {
    padding: 1rem;
  }
}

@media (min-width: 62em) {
  .fr-header__logo:not(:last-child), .fr-header__operator:not(:last-child), .fr-header__service:not(:last-child) {
    margin-right: 0.5rem;
  }
}

.fr-header__logo {
  order: 1;
  flex: 0 0 auto;
  padding: 0.75rem;
}

.fr-header__logo .fr-logo {
  font-size: 0.7875rem;
  margin: -1em;
}

.fr-header__logo .fr-logo::before {
  width: 2.0625rem;
  height: 0.75rem;
  margin-bottom: 0.25rem;
  background-size: 2.0625rem 0.84375rem, 2.0625rem 0.75rem, 0;
  background-position: 0 -0.04688rem, 0 0, 0 0;
}

:root[data-fr-mourning] .fr-header__logo .fr-logo::before {
  background-size: 2.0625rem 0.84375rem, 0, 2.0625rem 0.75rem;
}

.fr-header__logo .fr-logo::after {
  min-width: 1.96875rem;
  background-size: 3.9375rem 2.8125rem;
  background-position: 0 calc(100% + 1.40625rem);
  padding-top: 1.65625rem;
}

[data-fr-theme="dark"] .fr-header__logo .fr-logo::after {
  background-position: -1.96875rem 100%;
}

.fr-header__operator {
  padding: 0.75rem;
  order: 2;
  flex: 0 1 auto;
}

.fr-header__operator img {
  display: block;
}

.fr-header__service {
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  margin-left: 0.75rem;
  margin-right: 0.75rem;
  width: 100%;
}

@media (min-width: 62em) {
  .fr-header__service {
    margin-left: 0;
    margin-right: 0;
  }
}

@media (min-width: 62em) {
  .fr-header__service {
    width: auto;
  }
}

.fr-header__service-title {
  font-weight:  700 ;
  font-size:  1.125rem ;
  line-height:  1.5rem ;
}

@media (min-width: 48em) {
  .fr-header__service-title {
    font-size:  1.25rem ;
    line-height:  1.75rem ;
  }
}

.fr-header .fr-modal {
  overflow: auto;
  justify-content: initial;
}

.fr-header .fr-modal:not([role="dialog"]) {
  transition: none;
}

.fr-header .fr-modal::before {
  content: none;
}

.fr-header .fr-modal::after {
  content: none;
}

@media (min-width: 62em) {
  .fr-header .fr-modal {
    margin: 0;
    width: auto;
    height: auto;
    visibility: inherit;
    opacity: 1;
    position: initial;
    background-color: transparent;
    overflow: initial;
  }
}

.fr-header .fr-modal > * > .fr-link--close {
  margin-bottom: 1.5rem;
}

@media (min-width: 62em) {
  .fr-header .fr-modal > * > .fr-link--close {
    display: none;
  }
}

.fr-header .fr-modal > .fr-container {
  pointer-events: all;
  padding-top: 1rem;
  padding-bottom: 4.5rem;
  height: 100%;
}

@media (min-width: 62em) {
  .fr-header .fr-modal > .fr-container {
    padding-top: 0;
    padding-bottom: 0;
    height: auto;
  }
}

.fr-header__navbar {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  padding: 0.25rem;
  margin-left: auto;
  order: 3;
  flex: 0 0 auto;
  align-self: flex-start;
  z-index: 2500;
}

@media (min-width: 62em) {
  .fr-header__navbar {
    display: none;
  }
}

.fr-header__navbar .fr-btn {
  font-size: 1rem;
  line-height: 1.5rem;
  min-height: 2.5rem;
  padding: 0.5rem 1.5rem;
  flex-direction: row;
  overflow: hidden;
  white-space: nowrap;
  max-width: 2.5rem;
  max-height: 2.5rem;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  flex: 0 0 auto;
  white-space: nowrap;
}

.fr-header__navbar .fr-btn::before {
  font: normal normal normal 1.5rem/1 dsfr-icon;
}

.fr-header__navbar .fr-btn::before {
  margin-left: 0;
  margin-right: 0.5rem;
  font-size: 1.5rem;
}

.fr-header__navbar .fr-btn--menu::before {
  font: normal normal normal 1.5rem/1 dsfr-icon;
  content: "";
}

.fr-header__navbar .fr-btn--search::before {
  font: normal normal normal 1.5rem/1 dsfr-icon;
  content: "";
}

@media (min-width: 62em) {
  .fr-header__tools {
    flex-direction: column;
    align-items: flex-end;
    text-align: right;
    padding-left: 1rem;
    padding-right: 1rem;
    margin-left: auto;
    flex: 1 0 auto;
  }
  .fr-header__tools-links + .fr-header__search {
    margin-top: 1rem;
  }
}

@media (min-width: 62em) {
  .fr-header__tools .fr-header__search {
    max-width: 25rem;
    margin-left: auto;
  }
}

.fr-header .fr-links-group .fr-link {
  font-size: 0.875rem;
  line-height: 1.5rem;
  min-height: 2rem;
  padding: 0.25rem 0.75rem;
  border-radius: 1rem;
  flex-direction: row;
  overflow: initial;
  max-width: none;
  max-height: none;
}

.fr-header .fr-links-group .fr-link::before {
  font: normal normal normal 1rem/1 dsfr-icon;
}

.fr-header .fr-links-group .fr-link::before {
  margin-left: -0.25rem;
  margin-right: 0.5rem;
  font-size: 1rem;
}

.fr-header__tools-links {
  display: none;
}

@media (min-width: 62em) {
  .fr-header__tools-links {
    display: initial;
  }
}

.fr-header__tools-links .fr-links-group {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  flex-wrap: wrap;
  margin-left: -1rem;
  margin-right: -1rem;
}

.fr-header__tools-links .fr-links-group > *:not(:last-child) > .fr-link::after,
.fr-header__tools-links .fr-links-group > .fr-link:not(:last-child)::after {
  content: "";
  display: block;
  position: relative;
  right: -1rem;
  width: 1px;
  height: 1rem;
}

.fr-header__tools-links .fr-link {
  overflow: initial;
  margin-left: 0.25rem;
  margin-right: 0.25rem;
}

.fr-header .fr-header__menu-links .fr-links-group {
  display: flex;
  flex-direction: column;
}

.fr-header .fr-header__menu-links .fr-links-group .fr-link {
  border-radius: 0;
  width: 100%;
  min-height: 3rem;
}

@media (min-width: 62em) {
  .fr-header .fr-header__menu-links {
    display: none;
  }
}

@media (min-width: 62em) {
  .fr-header .fr-nav {
    justify-content: flex-start;
  }
  .fr-header .fr-nav__list {
    max-width: calc(100% + 1rem);
  }
  .fr-header .fr-nav .fr-menu::after,
  .fr-header .fr-nav .fr-mega-menu {
    box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.1), 0 8px 16px -16px rgba(0, 0, 0, 0.32);
  }
}

.fr-header {
  --blend: var(--background-elevated-grey-blend);
  background-color: var(--background-elevated-grey);
}

.fr-header__service {
  box-shadow: inset 0 1px 0 0 var(--border-default-grey);
}

@media (min-width: 62em) {
  .fr-header__service {
    box-shadow: none;
  }
}

.fr-header__menu-links .fr-link {
  box-shadow: inset 0 -1px 0 0 var(--border-default-grey);
}

.fr-header__tools-links .fr-link::after {
  box-shadow: inset 0 0 0 1px var(--border-default-grey);
}

.fr-header__navbar .fr-service__title {
  color: var(--text-title-grey);
}

.fr-header__navbar .fr-btn {
  background-color: transparent;
  --blend: inherit;
  color: var(--text-action-high-blue-france);
}

.fr-header__navbar .fr-btn--menu {
  color: var(--text-default-grey);
}

@media (min-width: 62em) {
  .fr-header .fr-header__menu {
    box-shadow: inset 0 1px 0 0 var(--border-default-grey) !important;
  }
}

.fr-header .fr-modal {
  --blend: var(--background-elevated-grey-blend);
  background-color: var(--background-elevated-grey);
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  /**
  * Correctif valeur 'initial' non prise en compte par IE
  */
}

@media all and (-ms-high-contrast: none) and (min-width: 62em), (-ms-high-contrast: active) and (min-width: 62em) {
  .fr-header__tools-links {
    display: block;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-header__tools-links .fr-link {
    overflow: visible;
  }
}

@media all and (-ms-high-contrast: none) and (min-width: 62em), (-ms-high-contrast: active) and (min-width: 62em) {
  .fr-header .fr-modal {
    position: static;
    overflow: visible;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-header {
    background-color: #fff;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-header__service {
    box-shadow: inset 0 1px 0 0 #e5e5e5;
  }
}

@media (min-width: 62em) {
  .fr-header__service {
    box-shadow: none;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-header__menu-links .fr-link {
    box-shadow: inset 0 -1px 0 0 #e5e5e5;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-header__tools-links .fr-link::after {
    box-shadow: inset 0 0 0 1px #e5e5e5;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-header__navbar .fr-service__title {
    color: #161616;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-header__navbar .fr-btn {
    background-color: transparent;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-header__navbar .fr-btn {
    color: #000091;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-header__navbar .fr-btn--menu {
    color: #3a3a3a;
  }
}

@media all and (min-width: 62em) and (-ms-high-contrast: none), (min-width: 62em) and (-ms-high-contrast: active) {
  .fr-header .fr-header__menu {
    box-shadow: inset 0 1px 0 0 #e5e5e5 !important;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-header .fr-modal {
    background-color: #fff;
  }
}
